@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&display=swap);
@font-face {
    font-family: "Slick";
    src: url(/static/media/ShareTechMono-Regular.8b302ac6.ttf);
}

.body {
	flex-basis: 30%;
}

.masterContainer {
    text-align: center;
		margin-left: 2%;
		margin-right: 2%;
}
.masterContainer .text {
    color: white;
    font-family: "Bebas Neue";
    margin-top: 1%;
}

.graphContainer {
    background-color: rgba(180, 180, 180, 0.7);
    width: 100%;
    border-radius: 1.5rem;
    font-family: "Slick";
    padding: 1%;
    margin-top: 0.5%;
    
    display: flex;
    justify-content: center;
}
.graphContainer .graphText {
    font-family: "Slick";
}
.graphContainer table { 
    width: 99%;
    text-align: center;
    background-color: rgba(0, 0, 0, 0);
    border-collapse: collapse; 
}
.graphContainer table th {
    border-bottom: 1px solid rgb(44, 44, 44);
    height: 2rem;
    text-align: right;
}
.graphContainer table td {
    height: 3.5rem;
    padding-bottom: 0.5rem;
}
.graphContainer table .headerCol{
    text-align: right;
}
.graphContainer .experienceBar {
    color: white;
    background-color: rgb(72, 59, 104);
    border-radius: 10px;
    margin-left: 1rem;
    width: auto;
    height: 90%;

    display: flex;
    align-items: center;
    justify-content: right;
    text-align: right;
    font-size: 0px;

    transition: font-size 0.1s ease-in-out, background-color 0.1s ease-in-out;
}
.graphContainer .experienceBar:hover {
    color: white;
    background-color: rgb(118, 97, 172);
    font-size: 100%;
}

.flexDiv {
    width: 100%;

    display: flex;
    justify-content: space-between;
}

.projectText {
    font-size: 1.5rem;
    margin-top: 1rem;
    margin-bottom: 1rem;
}

/*
 * =====================================================================================================================
 * Styling for mobile (width == or below 500px) below!
 */
 @media (max-width: 680px) {
    .body {
        display: none;
    }
    .masterContainer .text {
        display: none;
    }
    
    .graphContainer {
        display: none;
    }
    .graphContainer .graphText {
        display: none;
    }
    .graphContainer table { 
        display: none;
    }
    .graphContainer table th {
        display: none;
    }
    .graphContainer table td {
        display: none;
    }
    .graphContainer table .headerCol{
        display: none;
    }
    .graphContainer table h5{
        display: none;
    }
    .graphContainer .experienceBar {
        display: none;
    }
}

.linkBlock {
    display: flex;
    width: 100%;
    height: 100%;
    justify-content: space-around;
    align-items: center;

    text-align: center;

    font-size: 25px;
}

.container {
    width: 30rem;
    height: 10rem;
    min-height: 100px;
    box-shadow: 0 0 1rem 0 rgba(0, 0, 0, .3); 
    border-radius: 10px;
    position: relative;
    z-index: 1;
    background: inherit;
    overflow: hidden;
    margin: 50px;
}

.container:before {
    content: "";
    position: absolute;
    background: inherit;
    z-index: -1;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    box-shadow: inset 0 0 2000px rgba(255, 255, 255, 0.6);
    -webkit-filter: blur(10px);
            filter: blur(10px);
    margin: -20px;
}

.container a {
    height: 45px;
    width: 45px;
    font-size: 45px;

    transition: color 0.2s ease-out, -webkit-transform 0.2s ease-in-out;

    transition: color 0.2s ease-out, transform 0.2s ease-in-out;

    transition: color 0.2s ease-out, transform 0.2s ease-in-out, -webkit-transform 0.2s ease-in-out;
}
.container a:link {
    text-decoration: none;
    color: rgb(0, 0, 0);
}
.container a:visited {
    text-decoration: none;
    color: #2c0e47;
}
.container a:hover {
    -webkit-transform: scale(1.6, 1.6);
            transform: scale(1.6, 1.6);
    color: rgb(52, 41, 204);
}

